import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import trainingMajorApi from "api/common/trainingMajor";
import { toast } from "react-toastify";

export default function TableFilter({
    currentFilter,
    handleFilterOnClick,
    handleCourseFilterOnChange,
    handleTrainingMajorFilterOnChange,
}) {
    const { t } = useTranslation();
    const today = new Date();
    const [listtrainingMajor, setlisttrainingMajor] = useState([]);

    useEffect(async () => {
        try {
            const listtrainingMajorRes = await trainingMajorApi.getAll();
            if (Array.isArray(listtrainingMajorRes?.contents)) {
                setlisttrainingMajor(listtrainingMajorRes.contents);
            }
        } catch (err) {
            setlisttrainingMajor([])
        }
    }, []);

    return (
        <Form className="d-flex flex-wrap">
            <div style={{ display: "flex" }}>
                <FormGroup>
                    <Label for="nganhDaoTao" className="col-lg">
                        {t("admin.sub_major_list.MAJOR")}
                    </Label>
                    <Input
                        type="select"
                        value={
                            currentFilter.nganhDaoTao !== null
                                ? currentFilter.nganhDaoTao
                                : -1
                        }
                        onChange={handleTrainingMajorFilterOnChange}
                    >
                        <option key={1} value={-1}>
                            {t("admin.sub_major_edit.CHOOSE_MAJOR")}
                        </option>
                        {listtrainingMajor.length > 0 &&
                            listtrainingMajor.map((item, index) => {
                                return (
                                    <option key={index + 1} value={item.id}>
                                        {t("admin.sub_major_list.MAJORS_NAME")}:{" "}
                                        {`${item.ten} - ${item?.chuongTrinhDaoTao?.maCTDT}`}
                                    </option>
                                );
                            })}
                    </Input>
                </FormGroup>
                &emsp;
                <FormGroup>
                    <Label for="khoa" className="col-lg-3">
                        {t("admin.sub_major_list.COURSE")}
                    </Label>
                    <Input
                        type="number"
                        value={currentFilter?.khoa}
                        onChange={handleCourseFilterOnChange}
                    ></Input>
                </FormGroup>
                &emsp;
            </div>
            <FormGroup className="align-self-end">
                <Button onClick={handleFilterOnClick}>
                    <FaFilter /> {t("admin.common.FILTER")}
                </Button>
            </FormGroup>
        </Form>
    );
}
