export function exportHead() {
  const head = [];
  head.push("Major");
  head.push("Academic Year");
  head.push("Target");
  head.push("Opportunities Chance");
  head.push("Created at");
  head.push("Created by");
  head.push("Updated at");
  head.push("Updated by");
  return head;
}

export function exportRow(data) {
  const row = [];
  if (data.nganhDaoTao) {
    row.push(
      `${data.nganhDaoTao.maNganhDaoTao} - ${
        data.nganhDaoTao.chuongTrinhDaoTao
          ? data.nganhDaoTao.chuongTrinhDaoTao.maCTDT
          : ""
      } - ${
        data.nganhDaoTao.chuongTrinhDaoTao
          ? data.nganhDaoTao.chuongTrinhDaoTao.trinhDo
          : ""
      }`
    );
  } else {
    row.push("Unknown");
  }
  row.push(data.khoa);
  row.push(data.mucTieuChung);
  row.push(data.coHoiNgheNghiep);
  row.push(data.createdAt);
  row.push(data.createdBy ? data.createdBy.email : "Unknown");
  row.push(data.updatedAt);
  row.push(data.updatedBy ? data.updatedBy.email : "Unknown");
  return row;
}
